import React, { Fragment } from 'react';

import Navigation from './Navigation';

const Header = ({
  bgImage,
}) => {
  return (
    <Fragment>
      <header>
        <Navigation />
      </header>
      <style jsx>{`

        header {
          background: url(${bgImage}) no-repeat center / cover;
          height: 400px;
          width: 100%;
        }

        `}</style>
    </Fragment>
  );
};

export default Header;
